<template>
  <div v-show="appearRelease==0">
    <van-actionsheet
      v-model="show"
      cancel-text=""
      :actions="actions"
      @select="onSelect"/>
  </div>
</template>

<script>
  import bus from "../assets/eventBus"
    export default {
        name: "release",
      props: ['appear'],
      data() {
        return {
          appearRelease: 0,
          show: false,
          actions: [{
            name: '',
            className: 'van-active',
            id: 0,
          },
            {
              name: '',
              className: 'van-active',
              id: 1,
            },
          ]
        };
      },
      mounted: function() {
        bus.$on('cahngeRelease', function(data) {
          if (data == 1) {
            this.show = true;
          }
        }.bind(this))
      },
      methods: {
        openRelease: function() {
          this.show = true;
        },
        onSelect(item) {
          if (item.id == 0) {
            this.$router.replace("/releasePhoto")
            console.log('')
          } else {
            this.$router.replace("/releaseVideo")
            console.log('')
          }
          // 
          this.show = false;
        }
      },
      watch: {
        appear: function(n) {
          this.tabbarShow = n;
        }
      }
    }
</script>

<style>
  /*  css*/
  .van-actionsheet__name{
    color: #e55583 !important;
  }
</style>



